<template>
    <div v-if="this.task">
        <h4>Task</h4>
        <div>
            <label>Description: </label> {{this.task.description}}
        </div>
        <div>
            <label>Date: </label> {{this.task.date}}
        </div>
        <div>
            <label>Priority: </label> {{this.task.priority}}
        </div>
        <button class="button is-samll btn-danger" @click="deleteTask()">Delete</button>
    </div>
    <div v-else>
        <br/>
        <p>Please click on a task</p>
    </div>
</template>
<script lang="ts">
import  http  from '../http-common';

export default {
    name: 'task',
    props: ['task'],
    methods :{
        deleteTask () {
            http
                .delete('/tasks/' + this.task.id)
                .then(res => {
                    console.log(res.data);
                    this.$emit('refresh');
                    this.$router.push('/');
                })
                .catch(err =>{
                    console.log(err);
                })
        }
    }
}
</script>