<template>
  <div class="list row">
    <div div class ="col-md-6">
      <h4>Tasks List</h4>
      <ul>
        <li v-for="(task, index) in tasks" :key="index">
          <router-link :to="{
            name: 'task-details',
            params: { task: task, id: task.id }
          }">
              {{task.description}}
          </router-link>
        </li>
      </ul>
    </div>
    <div class="col-md-6">
      <router-view @refreahData="refresh"/>
    </div>
  </div>
</template>

<script>
import http from '../http-common';


export default {
  name: 'home',
  data () {
    return {
      tasks: []
    }
  },
  methods: {
    retriveTasks () {
      http
          .get('/tasks')
          .then(res => {
            this.tasks = res.data;
            console.log(res.data);
          })
          .catch(err => {
            console.log(err);
          })
    },
    refresh () {
      this.retriveTasks();
    }
  },
  mounted () {
    this.retriveTasks();
  }
}
</script>

<style>
.list {
  text-align: left;
  max-width: 450px;
  margin: auto;
}
</style>